<!--  -->
<template>
  <div class="">
    <!-- 下拉选择框 -->
    <el-select
      v-model="donePaperId"
      placeholder="选择试卷"
      @change="changeType()"
    >
      <el-option
        v-for="item in donePapers"
        :key="item.paperName"
        :label="item.paperName"
        :value="item.paperId"
      >
      </el-option>
    </el-select>

    <el-button type="success" class="query-button" @click="getDonePaperDetail()"
      >查看考试分析</el-button
    >

    <el-button type="danger" class="query-button" @click="getPdf()"
      >导出为pdf</el-button
    >
    <div id="pdfDom">
      <div class="top">
        <div>
          <div>-</div>
          <div>-</div>
          <div>-</div>
          <div>-</div>
          <div>-</div>
          <div>-</div>
          <div>-</div>
        </div>
        <div>
          <div>-</div>
          <div>-</div>
          <div>-</div>
          <div>-</div>
          <div>-</div>
          <div>-</div>
          <div>-</div>
        </div>
      </div>
      <div class="big-title">
        <div>{{ currentPaperName }}试卷分析</div>
      </div>

      <div class="title">
        <div>考试分析折线图</div>
      </div>
      <div class="box-pie" style="height: 400px" ref="chart"></div>
      <div class="title">
        <div>分数分布情况</div>
      </div>
      <div class="box-pie" style="height: 500px" ref="chart2"></div>

      <div class="detail" v-if="showOrNot">
        <div>
          <div class="row">得分比例在90%~100%区间为良好</div>
          <div class="row">得分比例在80%~90%区间为良好</div>
          <div class="row">得分比例在70%~80%区间为中等</div>
          <div class="row">得分比例在60%~70%区间为及格</div>
          <div class="row">得分比例在0%~60%区间为不及格</div>
        </div>
      </div>

      <div class="title">
        <div>考试数据概览</div>
      </div>
      <div class="detail" v-if="showOrNot">
        <div>
          <div class="row">
            <span class="prefix">最高分：</span>{{ maxScore }}分（{{
              maxScoreStudentName
            }}）
          </div>
          <div class="row">
            <span class="prefix">最低分：</span>{{ minScore }}分（{{
              minScoreStudentName
            }}）
          </div>
          <div class="row">
            <span class="prefix">中位数：</span>{{ midScore }}分
          </div>
          <div class="row">
            <span class="prefix">平均分：</span>{{ average }}分
          </div>
          <div class="row">
            <span class="prefix">总人数：</span>{{ peopleCount }}人
          </div>

          <div class="row">
            <span class="prefix">及格率：</span>{{ passPercent }}%
          </div>
        </div>
      </div>

      <div class="title">
        <div>试卷做题情况</div>
      </div>
      <div v-for="(item, i) in subjectsSituation" :key="i" :index="i">
        <div class="detail1" v-if="showOrNot">
        
            <div class="row1">
              <span class="prefix">第{{ i + 1 }}题：</span>
              正确人数:{{ item.rightCount }} 正确率:{{ item.rightPercent }}%
            </div>
        
        </div>
      </div>
      <div class="title">
        <div>考试排名表格</div>
      </div>
      <div class="table" v-if="showOrNot">
        <el-table :data="dataList" border style="width: 100%">
          <el-table-column
            prop="studentName"
            header-align="center"
            align="center"
            label="姓名"
          >
          </el-table-column>
          <el-table-column
            prop="score"
            header-align="center"
            align="center"
            label="分数"
          >
          </el-table-column>
          <el-table-column
            prop="position"
            header-align="center"
            align="center"
            label="排名"
          >
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      subjectsSituation: [],
      midScore: 0,
      passPercent: 0,
      htmlTitle: "pdf文件名",
      showOrNot: false,
      currentPaperName: "",
      minScore: 0,
      minScoreStudentName: "",
      maxScore: 0,
      maxScoreStudentName: "",
      average: 0,
      peopleCount: 0,
      dataList: [],
      //做过的试卷集合
      donePapers: [],
      //查询试卷id
      donePaperId: undefined,
      //用户token
      token: undefined,
      chart: null,
      // 配置可视化图形
      option: {
        xAxis: {
          type: "category",
          data: ["11分", "15分", "23分"],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            data: [2, 5, 15],
            type: "line",
            symbol: "triangle",
            symbolSize: 20,
            lineStyle: {
              color: "#5470C6",
              width: 4,
              type: "dashed",
            },
            itemStyle: {
              borderWidth: 3,
              borderColor: "#EE6666",
              color: "yellow",
            },
          },
        ],
      },
      //饼状图
      option2: {
        title: {
          text: "分布情况",
          subtext: "试卷分析",
          left: "center",
        },
        tooltip: {
          trigger: "item",
        },
        legend: {
          orient: "vertical",
          left: "left",
        },
        series: [
          {
            name: "详情",
            type: "pie",
            radius: "50%",
            data: [
              { value: 1048, name: "Search Engine" },
              { value: 735, name: "Direct" },
              { value: 580, name: "Email" },
              { value: 484, name: "Union Ads" },
              { value: 300, name: "Video Ads" },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      },
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    changeType() {
      console.log(this.donePaperId);
      this.donePapers.forEach((o) => {
        if (o.paperId == this.donePaperId) {
          this.currentPaperName = o.paperName + "-";
        }
      });
    },
    getPage() {
      // 引用chart并初始化
      this.chart = this.$echarts.init(this.$refs.chart);
      // 使用刚指定的配置项和数据显示图表。
      this.chart.setOption(this.option);
    },
    getPage2() {
      // 引用chart并初始化
      this.chart = this.$echarts.init(this.$refs.chart2);
      // 使用刚指定的配置项和数据显示图表。
      this.chart.setOption(this.option2);
    },
    //查询已做试卷集合
    getDonePapers() {
      this.$http
        .get("courseSystem/donePaper/getDonePaper", {
          headers: {
            token: this.token,
          },
        })
        .then(({ data }) => {
          console.log(data);
          this.donePapers = data.data;
        });
    },
    getDonePaperDetail() {
      var paperId = this.donePaperId;
      this.$http
        .get("courseSystem/donePaper/getDonePaperDetail/" + paperId, {
          headers: {
            token: this.token,
          },
        })
        .then(({ data }) => {
          console.log(data.data);
          this.minScore = data.minScore;
          this.minScoreStudentName = data.minScoreStudentName;
          this.maxScore = data.maxScore;
          this.maxScoreStudentName = data.maxScoreStudentName;
          this.peopleCount = data.peopleCount;
          this.average = data.average;
          this.option.xAxis.data = data.data.scores;
          this.option.series[0].data = data.data.peopleCount;
          this.dataList = data.dataList;
          this.showOrNot = true;
          this.option2.series[0].data = data.pieChartVos;
          this.passPercent = data.passPercent;
          this.midScore = data.midScore;
          this.subjectsSituation = data.subjectsSituation;
          this.getPage();
          this.getPage2();
        });
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    //从vuex中获得用户token
    this.token = this.$store.state.user.token;
    this.getDonePapers();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    // this.getPage();
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
.detail1 {
  margin: 0 auto;
  width: 50%;
  display: flex;
  background-color: #e9e7ef;
  padding-left: 260px;
}
.row1 {
  font-size: 27px;
  color: #666666;
  margin-top: 5px;
  margin-bottom: 5px;
}
.big-title {
  display: flex;
  justify-content: center;
  font-weight: bold;
  color: #676767;
  font-size: 40px;
  font-family: "楷体";
}
.top {
  display: flex;
  justify-content: space-between;
}
.query-button {
  margin-left: 20px;
}
.table {
  display: flex;
  justify-content: center;
  margin: 10px auto;
  width: 50%;
}
.title {
  margin: 20px auto;
  width: 50%;
  display: flex;
  justify-content: center;
  /* background-color: #e9e7ef; */
  padding: 10px 0px;
  border-radius: 15px;
  font-weight: bold;
  color: #676767;
  font-size: 30px;
  font-family: "楷体";
}
.detail {
  margin: 0 auto;
  width: 50%;
  display: flex;
  justify-content: center;
  background-color: #e9e7ef;
  padding: 10px 0px;
  border-radius: 15px;
}
.row {
  font-size: 27px;
  color: #666666;
  margin-top: 5px;
}
.prefix {
  font-weight: bold;
  color: #676767;
  font-size: 30px;
  font-family: "楷体";
}
</style>